@use '../base/mixins' as m;
@use '../base/variables' as v; 

.bienvenida {
    margin: 8rem 0 5rem 0;

    text-align: center;

    &__heading {
        @include m.headingCursiva; 
    }

    &__texto {
        font-size: 2rem;
        color: v.$gris;
        max-width: 50rem;
        margin: 4rem auto 0 auto;
        line-height: 2;
    }
}