@use '../base/mixins' as m;
@use '../base/variables' as v;

.cards {
    &__grid {
        @include m.tablet {
            @include m.grid(3, 2rem); 
            
        }
    }
}

.card {
    max-width: 35rem;
    margin: 0 auto 2rem auto;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet {
        max-width: unset;
        margin: 0;
    }

    &__imagen {
        width: 100%;
        border: 0.5rem solid v.$blanco;

        transition: transform 0.2s;

        &:hover {
            transform: rotate(-4deg) scale(1.05);
        }
    }

    &__contenido {
        max-width: 90%;
        margin: -5rem auto 0 auto ;
        background-color: v.$blanco;
        position: relative;
        padding: 2rem 1rem; 

    }

    &__heading {
        @include m.headingCursiva; 
        font-size: 3.4rem;
        padding: 1.5rem 0; ;

    }

    &__enlace {
        @include m.boton(v.$secundario, v.$primario); 
        display: block;
    }
}