@use '../base/mixins' as m;
@use '../base/variables' as v;

.productos {
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem; 
    }

    &__grid {
        @include m.grid(1, 2rem); 

        @include m.tablet {
            grid-template-columns: repeat(2, 1fr)
        }
        
        @include m.desktop {
            grid-template-columns: repeat(4, 1fr)
        }
    }
}

.producto {
    max-width: 50rem;
    margin: 0 auto;

    &__contenido {
        background-color: v.$blanco;
        padding: 2rem;
    }

    &__mask {
        overflow: hidden;
    }

    &__imagen {
        width: 100%;
        transition: transform 0.2s;

        &:hover {
            transform: scale(1.2); 
        }
    }

    &__nombre {
        @include m.heading; 
        font-size: 2.4rem;
        color: v.$secundario;
    }
    &__descripcion {
        font-size: 1.6rem;
        text-transform: uppercase;
    }

    &__precio {
        text-align: center;
        color: v.$gris;
        font-size: 2.8rem;
        margin: 0;
    }
}