@use '../base/mixins' as m;

.header {
    margin-top: 3rem;

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo {
        width: 20rem;
    }
}